<template>
  <div class="cartIcon" @click="gotoShoppingHandler">
    <font-awesome-icon :icon="['fas', 'shopping-cart']" />
    <span v-show="cartLength > 0">
      {{ cartLength }}
    </span>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  computed: {
    ...mapState(['cartLength']),
  },
  methods: {
    gotoShoppingHandler() {
      if (this.cartLength <= 0) {
        this.$fire({
          title: '請先購物',
          type: 'error',
          timer: 3000,
        });
        this.$router.push({ name: 'products' });
        return;
      }
      this.$router.push({ name: 'shopping' });
    },
  },
};
</script>

