.cartIcon {
  font-size: 36px;
  text-align: center;
  position: fixed;
  right: 70px;
  bottom: 50px;
  color: #81c1ca;
  padding: 10px;
  z-index: 10;
  cursor: pointer;
  span {
    position: absolute;
    font-size: 24px;
    top: -5px;
    right: -10px;
    color: #000;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #81c1ca;
  }
  @media (max-width: 768px) {
    right: 20px;
  }
}
</style>
